import React,{Component} from 'react'
import { Form, Button, message, DatePicker, Select } from 'antd';
import moment from 'moment'
const FormItem = Form.Item;
const { RangePicker } = DatePicker;
const Option = Select.Option;

export class SearchForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            time: "",
            type: ""
        };
        ['handleSubmit',  'resetForm'].forEach(m=>this[m] = this[m].bind(this))
    }

    handleSubmit(page) {
        let that = this;
        this.props.form.validateFields((err, values) => {
            that.props.showLoading();
            let params = {};
            if(values.time) {
                params.startTime = moment(values.time[0], "YYYY-MM-DD HH:mm");
                params.endTime = moment(values.time[1], "YYYY-MM-DD HH:mm");
            }
            ajax({
                url: "/finance/consumption/getInfoList",
                data: Object.assign(params, values, {
                    current: page
                }),
                callback: function(data) {
                    that.props.hideLoading();
                    if(data.status == 200) {
                        that.props.searchFuc(data.data);
                    } else {
                        message.error(data.errMsg, 1);
                    }
                }
            })
        })
    }
    resetForm() {
        this.props.form.resetFields();
    }

    render() {
        const { getFieldDecorator } = this.props.form;
        return (
            <Form layout="inline">
                <FormItem
                    label="分类"
                >
                    {getFieldDecorator('type', {
                        initialValue: "0"
                    })(
                        <Select style={{ width: 150 }}>
                            <Option value="0">所有</Option>
                            <Option value="1">活动详情</Option>
                            <Option value="2">查看联系方式</Option>
                        </Select>
                    )}
                </FormItem>
                <FormItem
                    label="时间"
                >
                    {getFieldDecorator('time')(
                        <RangePicker
                            showTime={{ format: 'HH:mm' }}
                            format="YYYY-MM-DD HH:mm"
                            placeholder={['开始时间', '结束时间']}
                         />
                    )}
                </FormItem>
                <FormItem>
                    <Button icon="search" type="primary"  onClick={this.handleSubmit.bind(this,1)} style={{"marginRight": "20px"}}>查询</Button>
                    <Button icon="reload" onClick={this.resetForm}>重置</Button>
                </FormItem>
            </Form>
            )
    }
}

const WrappedSearchForm = Form.create({})(SearchForm);

module.exports = WrappedSearchForm;


